import * as Yup from 'yup';

const requiredMessage = '–û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ';
const tooShortMessage = '–ú–∏–Ω–∏–º—É–º 2 —Å–∏–º–≤–æ–ª–∞';
const phoneRegExp = /^\+?(?:38)?\s?\(?0\s?\d{2}\)?[\s-]?\d{3}[\s-]?\d{2}[\s-]?\d{2}$/;
const passwordRegExp = /^(?=.*[A-Za-z–ê-–Ø–∞-—è])(?=.*\d)[A-Za-z–ê-–Ø–∞-—è\d]{8,}$/u;

export default Yup.object().shape({
  firstName: Yup.string()
    .required(requiredMessage)
    .min(2, tooShortMessage)
    .default(''),
  lastName: Yup.string()
    .required(requiredMessage)
    .min(2, tooShortMessage)
    .default(''),
  login: Yup.string()
    .required(requiredMessage)
    .min(2, tooShortMessage)
    .max(20, '–°–ª–∏—à–∫–æ–º –º–Ω–æ–≥–æ —Å–∏–º–≤–æ–ª–æ–≤')
    .default(''),
  email: Yup.string()
    .required(requiredMessage)
    .email('–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –ø–æ—á—Ç—ã')
    .default(''),
  telephone: Yup.string()
    .required(requiredMessage)
    .default('')
    .matches(phoneRegExp, '–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –Ω–æ–º–µ—Ä–∞'),
  password: Yup.string()
    .required(requiredMessage)
    .default('')
    .matches(passwordRegExp, {
      message: '–î–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å 8 —Å–∏–º–≤–æ–ª–æ–≤, –∑–∞–≥–ª–∞–≤–Ω—É—é –±—É–∫–≤—É –∏ —Ü–∏—Ñ—Ä—É',
      excludeEmptyString: true,
    }),
  repeatPassword: Yup.string().default('').when('password', {
    is: '',
    then: Yup.string().notRequired(),
    otherwise: Yup
      .string()
      .required('–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –≤–∞—à –ø–∞—Ä–æ–ª—å')
      .oneOf([Yup.ref('password')], '–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç'),
  })
  ,
});
