import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from '@material-ui/core';
import SigninForm from './SigninForm';
import { useStyles } from './styles';
import { useActions } from '../../hooks/useActions';

const userLoginSelector = (state) => state.userLogin;

const Signin = () => {
  const { userInfo } = useSelector(userLoginSelector);
  const { logout } = useActions();
  const classes = useStyles();

  return (
    <section className='signin-page'>
      <div className={classes.wrapper}>
        {userInfo
          ? (
            <>
              <h3>–í—ã —É–∂–µ –ø—Ä–µ–¥—Å—Ç–∞–≤–∏–ª–∏—Å—å —Å–∏—Å—Ç–µ–º–µ</h3>
              <p>
                –î–ª—è –≤—ã—Ö–æ–¥–∞ –∏–∑ –∞–∫–∫–∞—É–Ω—Ç–∞&nbsp;
                <Link className={classes.link} onClick={logout}>–Ω–∞–∂–º–∏—Ç–µ –∑–¥–µ—Å—å</Link>
              </p>
            </>
          ) : (
            <>
              <h2 className={classes.title}>–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</h2>
              <SigninForm />
            </>
          )}
      </div>
    </section>
  );
};

export default Signin;
